<template>
  <div>
    <!--  -->
    <div class="top-nav">
      <div class="nav-left">
        <span></span>
      </div>
      <div class="nav-right">
        <span @mouseover="showUserInfo" @mouseleave="handleMouseLeave">{{ userName || '...' }}</span>
        <button @click="logout"></button>
      </div>
    </div>

    <!--  -->
    <div v-if="showInfo && userInfo" class="user-info-popup" @mouseenter="keepUserInfoVisible" @mouseleave="hideUserInfo">
      <p>: {{ userInfo.data.userName }}</p>
      <p>: {{ userInfo.data.sex }}</p>
      <p>: {{ userInfo.data.cardId }}</p>
      <p>: {{ userInfo.data.email }}</p>
      <button @click="editUserInfo"></button>
    </div>

    <!--  -->
    <div v-if="showEditForm" class="modal-overlay">
      <div class="edit-form-popup">
        <h3></h3>
        <!--  -->
        <div class="scrollable-container">
          <form @submit.prevent="submitEditForm">

            <label for="userName">:</label>
            <input type="text" v-model="userInfo.data.userName" id="userName" />


            <label for="email">:</label>
            <input type="email" v-model="userInfo.data.email" id="email" />

            <label for="sex">:</label>
            <select v-model="userInfo.data.sex" id="sex">
              <option value=""></option>
              <option value=""></option>
            </select>

            <label for="birthday">:</label>
            <input type="date" v-model="userInfo.data.birthday" id="birthday" />

            <label for="cardId">:</label>
            <input type="text" v-model="userInfo.data.cardId" id="cardId" />

            <label for="phone">:</label>
            <input type="tel" v-model="userInfo.data.phone" id="phone" />

            <label for="address">:</label>
            <input type="text" v-model="userInfo.data.address" id="address" />

            <label for="job">:</label>
            <input type="text" v-model="userInfo.data.job" id="job" />

            <label for="company">:</label>
            <input type="text" v-model="userInfo.data.company" id="company" />

            <label for="workYears">:</label>
            <input type="number" v-model="userInfo.data.workYears" id="workYears" min="0" />

            <label for="yearIncome">():</label>
            <input type="number" v-model="userInfo.data.yearIncome" id="yearIncome" min="0" step="0.01" />

            <label for="ismarry">:</label>
            <select v-model="userInfo.data.ismarry" id="ismarry">
              <option value=""></option>
              <option value=""></option>
            </select>

            <label for="age">:</label>
            <input type="number" v-model="userInfo.data.age" id="age" readonly />

            <label for="role">:</label>
            <select v-model="userInfo.data.role" id="role">
              <option value="USER"></option>
              <option value="ADMIN"></option>
            </select>

            <div class="buttons">
              <button type="submit"></button>
              <button type="button" @click="closeEditForm"></button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="content">
      <div class="left-nav">
        <ul>
          <li @click="changeTab('loanProjects')" :class="{ active: currentTab === 'loanProjects' }"></li>
          <li @click="changeTab('accountInfo')" :class="{ active: currentTab === 'accountInfo' }"></li>
          <li @click="changeTab('bankStatements')" :class="{ active: currentTab === 'bankStatements' }"></li>

        </ul>
      </div>
      <div class="right-content">
        <component :is="currentComponent"></component>
      </div>
    </div>
  </div>
</template>


<script>
import LoanProjects from './LoanProjects.vue';
import AccountInfo from './AccountInfo.vue';
import BankStatements from './BankStatements.vue';

import axios from 'axios';



export default {
  data() {
    return {
      userInfo: null,  // 
      userName: '',     // 
      showInfo: false,  // 
      showEditForm: false, // 
      currentTab: 'loanProjects', // Tab
      components: {
        loanProjects: LoanProjects,
        accountInfo: AccountInfo,
        bankStatements: BankStatements,

      }
    };
  },
  mounted() {
    const userInfoJson = localStorage.getItem('userInfo'); //  JSON\
    console.log(' localStorage :', userInfoJson);

    if (userInfoJson) {
     try{
       //  JSON 
       this.userInfo = JSON.parse(userInfoJson);
       console.log(':', this.userInfo);
       this.userName = this.userInfo.data.userName; //  Vue  data 
       console.log(':', this.userName); // 
     }catch (error) {
       console.error(':', error);
       alert('');
       this.$router.push({ name: 'Login' }); // 
     }
    } else {
      console.error('');
      alert('');
      this.$router.push({ name: 'Login' }); // 
    }
  },

  computed: {
    currentComponent() {
      return this.components[this.currentTab];
    }
  },


  methods: {
    // 
    showUserInfo() {
      this.showInfo = true;
    },
    // 
    hideUserInfo() {
      this.showInfo = false;
    },
    // 
    keepUserInfoVisible() {
      this.showInfo = true;
    },
    // 
    handleMouseLeave() {
      this.showInfo = false;
    },
    // Tab
    changeTab(tab) {
      this.currentTab = tab;
    },
    // 
    logout() {
      localStorage.clear();  //  localStorage 
      this.$router.push('/'); // 
    },
    // 
    editUserInfo() {
      this.showEditForm = true;
    },
    // 
    submitEditForm() {
      console.log(' userId:', this.userInfo.data.userId); //  userId
      axios.put('http://localhost:8080/user/edit', this.userInfo.data, {
        headers: {
          role: 'USER',
          session: this.userInfo.data.userId,
        }
      })
          .then(response => {
            console.log(':', response.data);
            //  userInfo 
            if (response.data) {
              this.userInfo = response.data;
              this.userName = this.userInfo.data.userName;
              localStorage.setItem('userInfo', JSON.stringify(this.userInfo)); // 
            }
            this.showEditForm = false;

          })
          .catch(error => {
            console.error(':', error);
          });
      this.showInfo = false;
    },
    // 
    closeEditForm() {
      this.showEditForm = false;
      this.showInfo = false;
    }
  }
};
</script>

<style scoped>
/*  */
.top-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  z-index: 100;
}

.nav-left {
  font-size: 18px;
}

.nav-right {
  display: flex;
  align-items: center;
}

.nav-right span {
  margin-right: 20px;
  cursor: pointer;
}
/*  */
.user-info-popup {
  position: absolute;
  top: 35px;
  right: 20px;
  background-color: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  width: 250px;
  z-index: 200;
  animation: fadeIn 0.3s ease;
}

.nav-right button {
  background-color: #a83434;
  color: white;
  border: none;
  padding: 6px 27px;
  cursor: pointer;
}
/*  */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999; /*  */
}



.edit-form-popup {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;

  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}
.edit-form-popup h3 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}
.edit-form-popup form {
  display: grid;
  grid-template-columns: 1fr 4fr;
  gap: 10px;
}
.edit-form-popup label {
  text-align: right;
  padding-right: 10px;
  font-weight: bold;
}
.edit-form-popup input,
.edit-form-popup select,
.edit-form-popup button {
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.edit-form-popup input:focus,
.edit-form-popup select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 4px rgba(0, 123, 255, 0.5);
}
.edit-form-popup button {
  width: 100%;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}
.edit-form-popup button:hover {
  background-color: #0056b3;
}

.edit-form-popup .buttons {
  grid-column: span 2;
  display: flex;
  justify-content: space-between;
}

.edit-form-popup .buttons button {
  width: 48%;
}
/*  */
.scrollable-container {
  max-height: 400px; /*  */
  overflow-y: auto; /*  */
  padding-right: 10px;
}
/*  */
.scrollable-container::-webkit-scrollbar {
  width: 8px;
}
.scrollable-container::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 4px;
}
.scrollable-container::-webkit-scrollbar-thumb:hover {
  background-color: #aaa;
}
.content {
  display: flex;
  margin-top: 60px;
}

/*  */
.left-nav {
  position: fixed;
  top: 47px;
  left: 0;
  width: 200px;
  background-color: #333;
  padding: 0;
  height: calc(100vh - 47px);
  box-sizing: border-box;
}

.left-nav ul {
  list-style: none;
  padding: 0;
}

.left-nav li {
  padding: 10px;
  cursor: pointer;
  color: white;
  transition: background-color 0.3s;
}

.left-nav li:hover {
  background-color: #333;
}

.left-nav li.active {
  background-color: #a83434;
  font-weight: bold;
}

/*  */
.right-content {
  margin-left: 200px;
  padding: 0;
  width: calc(100% - 200px);
  height: calc(100vh - 60px);
  overflow-y: auto;
  background-color: #f4f4f4;
}
</style>
