<template>

  <div class="login">
    <!--  -->
    <div class="welcome-title-container">
      <h1 class="welcome-title">!</h1>
    </div>

    <div class="login-container">

      <h2></h2>

      <!--  -->
      <div>
        <label for="role"></label>
        <select v-model="selectedRole" @change="handleRoleChange">
          <option value="user"></option>
          <option value="clerk"></option>
          <option value="assessor"></option>
          <option value="administrator"></option>
        </select>
      </div>


      <!--  -->
      <div v-if="selectedRole === 'user'">
        <form @submit.prevent="handleSubmit">
          <div>
            <label for="cardId"></label>
            <input type="text" v-model="cardId" required />
          </div>
          <div>
            <label for="password"></label>
            <input type="password" v-model="password" required />
          </div>
          <button type="submit"></button>
        </form>
        <p v-if="!isUserLoggedIn">
          <router-link to="/register"></router-link>
        </p>
      </div>

      <!--  -->
      <div v-if="selectedRole === 'clerk'">
        <form @submit.prevent="handleSubmit">
          <div>
            <label for="clerkId"></label>
            <input type="text" v-model="clerkId" required />
          </div>
          <div>
            <label for="clerkPassword"></label>
            <input type="password" v-model="clerkPassword" required />
          </div>
          <button type="submit"></button>
        </form>
      </div>

      <!--  -->
      <div v-if="selectedRole === 'assessor'">
        <form @submit.prevent="handleSubmit">
          <div>
            <label for="assessorId"></label>
            <input type="text" v-model="assessorId" required />
          </div>
          <div>
            <label for="assessorPassword"></label>
            <input type="password" v-model="assessorPassword" required />
          </div>
          <button type="submit"></button>
        </form>
      </div>

      <!--  -->
      <div v-if="selectedRole === 'administrator'">
        <form @submit.prevent="handleSubmit">
          <div>
            <label for="adminId"></label>
            <input type="text" v-model="adminId" required />
          </div>
          <div>
            <label for="adminPassword"></label>
            <input type="password" v-model="adminPassword" required />
          </div>
          <button type="submit"></button>
        </form>
      </div>

    </div>


  </div>
</template>

<script>
import axios from 'axios';
import { useStore } from "vuex"


export default {
  data() {
    return {
      selectedRole: 'user', // 
      cardId: '', // 
      password: '',
      clerkId: '',
      clerkPassword: '',
      assessorId: '',
      assessorPassword: '',
      adminId: '',
      adminPassword: '',
      isUserLoggedIn: false, // 
    };
  },
  methods: {
    // 
    handleRoleChange() {
      this.cardId = '';
      this.password = '';
      this.clerkId = '';
      this.clerkPassword = '';
      this.assessorId = '';
      this.assessorPassword = '';
      this.adminId = '';
      this.adminPassword = '';
    },
    // 
    async handleSubmit() {
      let loginData = {};
      let apiUrl = '';

      // API URL
      if (this.selectedRole === 'user') {
        loginData = { cardId: this.cardId, password: this.password };
        apiUrl = 'http://localhost:8080/user/login';
      } else if (this.selectedRole === 'clerk') {
        loginData = { clerkId: this.clerkId, password: this.clerkPassword };
        apiUrl = 'http://localhost:8080/clerk/login';
      } else if (this.selectedRole === 'assessor') {
        loginData = { assessorId: this.assessorId, password: this.assessorPassword };
        apiUrl = 'http://localhost:8080/assessor/login';
      } else if (this.selectedRole === 'administrator') {
        loginData = { adminId: this.adminId, password: this.adminPassword };
        apiUrl = 'http://localhost:8080/administrator/login';
      }

      // 
      console.log('Login Data:', loginData); // 
      //
      try {
        // const response = await axios.post(apiUrl, loginData);
        const response = await axios.post(apiUrl, loginData, {
          headers: {
            'Content-Type': 'application/json'
          },
          withCredentials: true  //  session 
        });
        console.log('Response:', response); // 
        if (response.data.code=== 500 ){
          alert('');

        } else {
          alert('');
          localStorage.setItem('Role', this.selectedRole); // 


          // 
          if (this.selectedRole === 'user') {
            // 
            const userInfoResponse = await axios.post(
                'http://localhost:8080/user/getUserInfo',
                { cardId: this.cardId },
                {
                  headers: {
                    'Content-Type': 'application/json',
                  },
                  withCredentials: true, //  session 
                }
            );
            console.log('User Info Response:', userInfoResponse.data); // 
            localStorage.setItem('userInfo', JSON.stringify(userInfoResponse.data)); // 
            localStorage.setItem('token', userInfoResponse.data.token);

            this.$router.push({ name: 'UserHome' });  // 
          }
          else if (this.selectedRole === 'clerk') {

            localStorage.setItem('clerkInfo', JSON.stringify(response.data)); // 

            //localstorage

            this.$router.push({ name: 'ClerkHome' });  // 
          } else if (this.selectedRole === 'assessor') {

            localStorage.setItem('assessorInfo', JSON.stringify(response)); // 
            //
            this.$router.push({ name: 'AssessorHome' });  // 
          } else if (this.selectedRole === 'administrator') {
            localStorage.setItem('adminInfo', JSON.stringify(response.data)); // 
            //
            this.$router.push({ name: 'AdminHome' });  // 
          }



        }
      }catch (error) {
        if (error.response && error.response.status === 401) {
          alert('');
        } else {
          console.error('Error details:', error);
          alert('');
        }
      }
      //   // 
      //   const response = { data: { success: true } };
      //   if (response.data.success) {
      //     alert('');
      //     localStorage.setItem('userRole', this.selectedRole);
      //   } else {
      //     alert('');
      //   }
      // }
    }
  }
};
</script>

<style scoped>
.login {

  width: 100%;
  height:96vh;
  background-image:url('../assets/login.png');
  background-size: cover;
  background-position: center;
  flex-direction: column; /*  */
  display: flex;
  justify-content: center;
  align-items: center;
  background-attachment: fixed;
}
.login-container {
  background: rgba(255, 255, 255, 0.8); /*  */
  padding: 40px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}


form {
  display: flex;
  flex-direction: column;
  padding-left: 2px;

}
label {
  text-align: left;
  margin-bottom: 5px;
  font-weight: bold;
}
.welcome-title-container {
  margin-bottom: 20px; /*  */
}

.welcome-title {
  font-size: 32px;
  color: #333;
  font-weight: bold;
  text-align: center;
}

input {
  margin-bottom: 10px;
}

button {
  margin-top: 10px;
}
</style>
