import { createRouter, createWebHistory } from 'vue-router';
import Login from '../views/Login.vue';
import Register from '../views/Register.vue';
import UserHome from '../views/UserDashboard.vue';
import AdminHome from '../views/AdminDashboard.vue';
import ClerkHome from '../views/ClerkDashboard.vue';
import AssessorHome from '../views/AssessorDashboard.vue';


// 
const routes = [
    {
        path: '/',
        name: 'Login',
        component: Login,
    },
    {
        path: '/register',
        name: 'Register',
        component: Register,
    },
    {
        path: '/user/home',
        name: 'UserHome',
        component: UserHome,
        meta: { role: 'user'}  // 
    },
    {
        path: '/admin/home',
        name: 'AdminHome',
        component: AdminHome,
        meta: { role: 'administrator' }  // 
    },
    {
        path: '/clerk/home',
        name: 'ClerkHome',
        component: ClerkHome,
        meta: { role: 'clerk' }  // 
    },
    {
        path: '/assessor/home',
        name: 'AssessorHome',
        component: AssessorHome,
        meta: { role: 'assessor' }  // 
    },
];

const router = createRouter({
    history: createWebHistory(),

    //  base:process.env.BASE_URL,
    routes
})


// 
router.beforeEach((to, from, next) => {

    const Role = localStorage.getItem('Role')  //  localStorage 
    const requiredRole = to.meta.role

    if (requiredRole && Role !== requiredRole) {
        // 
        next({ name: 'Login' })
    } else {
        next()
    }
})

export default router;
