<template>
  <div class="container">
    <h2></h2>
    <div class="search-container">
      <input
          v-model="userId"
          type="number"
          placeholder="ID"
          @keypress.enter="fetchCreditReport"
      />
      <button @click="fetchCreditReport"></button>
    </div>

    <!--  -->
    <div v-if="totalDebt !== null" class="credit-info">
      <h3></h3>
      <p><strong>ID:</strong> {{ userId }}</p>
      <p><strong>:</strong> {{ totalDebt }} </p>
    </div>

    <!--  -->
    <div v-if="repaymentSchedule.length > 0" class="repayment-info">
      <h3></h3>
      <table>
        <thead>
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="loan in repaymentSchedule" :key="loan.loanId">
          <td>{{ loan.loanId }}</td>
          <td>{{ loan.totalRepay }} </td>
          <td>{{ loan.repayDate }}</td>
          <td>
            <span v-if="loan.status === 0" class="status-unpaid"></span>
            <span v-else class="status-paid"></span>
          </td>
        </tr>
        </tbody>
      </table>
    </div>

    <!--  -->
    <div v-if="errorMessage" class="error">
      <p>{{ errorMessage }}</p>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      userId: '',
      totalDebt: null,
      repaymentSchedule: [],
      errorMessage: ''
    };
  },
  methods: {
    fetchCreditReport() {
      if (!this.userId) {
        this.errorMessage = 'ID';
        return;
      }

      this.errorMessage = ''; // 
      this.repaymentSchedule = []; // 

      // 
      axios.get(`http://localhost:8080/creditreport/gettotaldebt/${this.userId}`)
          .then(response => {
            this.totalDebt = response.data || 0;
          })
          .catch(error => {
            this.errorMessage = '';
            console.error(error);
          });

      // ID
      axios
          .get(`http://localhost:8080/loan/findbyuserr/${this.userId}`,)
          .then(response => {
            const loanApplications = response.data;
            console.log("Id:",response.data );
            console.log(":",response.data.length );
            if (loanApplications && loanApplications.length > 0) {
              // 
              loanApplications.forEach(loan => {
                this.fetchRepaymentSchedule(loan.loanId);
              });
            } else {
              this.errorMessage = '';
            }
          })
          .catch(error => {
            this.errorMessage = '';
            console.error(error);
          });
    },

    // ID
    fetchRepaymentSchedule(loanId) {
      axios.get(`http://localhost:8080/repay/byloanid/${loanId}`)
          .then(response => {
            console.log("", response);
            const repaymentData = response.data || [];
            repaymentData.forEach(repayment => {
              this.repaymentSchedule.push({
                loanId: loanId,
                totalRepay:repayment.totalRepay,
                repayDate:repayment.repayDate,
                status:repayment.status,


              });
            });
          })
          .catch(error => {
            console.error(':', error);
          });
    }
  }
};
</script>

<style scoped>
.container {
  font-family: Arial, sans-serif;
  margin: 20px;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h2 {
  font-size: 24px;
  color: #333;
}

.search-container {
  margin: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;  /*  */
}

input {
  padding: 8px;
  margin-right: 10px;
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 8px 15px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

.credit-info, .repayment-info {
  margin-top: 20px;
}

h3 {
  font-size: 20px;
  color: #333;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

table th, table td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

table th {
  background-color: #f2f2f2;
}

.error {
  color: red;
  font-weight: bold;
  margin-top: 20px;
}
</style>
