<template>
  <div class="loan-projects">
    <!--  -->
    <div class="section">
      <h2 class="section-title"></h2>

      <!--  -->
      <div class="loan-filter">
        <label for="statusFilter"></label>
        <select v-model="selectedStatus" @change="filterLoans" class="select-filter">
          <option value=""></option>
          <option value="0"></option>
          <option value="1"></option>
          <option value="2"></option>
        </select>
      </div>

      <div v-if="filteredLoans.length > 0" class="loan-table">
        <table>
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="loan in filteredLoans" :key="loan.loanApplicationId">
            <td>{{ loan.loanApplicationId }}</td>
            <td>{{ loan.applicationmoney }}</td>
            <td>{{ loan.applicationtime }}</td>
            <td>{{ loan.accountId }}</td>
            <td>{{ loan.projectId }}</td>
            <td>{{ getStatusText(loan.status) }}</td>
            <td>
              <!--  -->
              <button v-if="loan.status === 1" @click="showRepaymentSchedule(loan.loanApplicationId)">
                
              </button>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <div v-else>
        <p></p>
      </div>
    </div>

    <!--  -->
    <div v-if="isModalVisible" class="repayment-modal">
      <div class="repayment-modal-content">

        <table class="repayment-table">
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-if="repaymentData.length === 0">
            <td colspan="7" class="no-data"></td>
          </tr>
          <tr v-else v-for="item in repaymentData" :key="item.repayId">
            <td>{{ item.repayId }}</td>
            <td>{{ item.principal }}</td>
            <td>{{ item.interest }}</td>
            <td>{{ item.overdueInterest }}</td>
            <td>{{ item.totalRepay }}</td>
            <td>{{ item.repayDate }}</td>
            <td>
              <span v-if="item.status === 0" class="status-unpaid"></span>
              <span v-else class="status-paid"></span>
            </td>
          </tr>
          </tbody>
        </table>
        <button class="close-button" @click="isModalVisible = false"></button>
      </div>
    </div>



    <!--  -->
    <div class="section">
      <h2 class="section-title"></h2>
      <div v-if="projects.length > 0" class="loan-table">
        <table>
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="project in projects" :key="project.projectId">
            <td>{{ project.projectId }}</td>
            <td>{{ project.projectName }}</td>
            <td>{{ project.loanmoneyMin }} - {{ project.loanmoneyMax }}</td>
            <td>{{ project.loanmonthMin }} - {{ project.loanmonthMax }}</td>
            <td>{{ project.rateMin }}% - {{ project.rateMax }}%</td>
          </tr>
          </tbody>
        </table>
      </div>
      <div v-else>
        <p></p>
      </div>
    </div>

    <!--  -->
    <div class="section">
      <h3 class="section-title"></h3>
      <form @submit.prevent="applyLoan" class="loan-form">
        <div class="form-group">
          <label for="accountId">:</label>
          <input type="number" id="accountId" v-model="loanForm.accountId" required />
        </div>
        <div class="form-group">
          <label for="projectId">:</label>
          <input type="number" id="projectId" v-model="loanForm.projectId" required />
        </div>
        <div class="form-group">
          <label for="applicationMoney">:</label>
          <input type="number" id="applicationMoney" v-model="loanForm.applicationMoney" required />
        </div>
        <div class="form-group">
          <label for="applicationTime">:</label>
          <input type="number" id="applicationTime" v-model="loanForm.applicationTime" required />
        </div>
        <button type="submit" class="submit-btn"></button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      userLoans: [], // 
      filteredLoans: [], // 
      projects: [], // 
      loanForm: {
        accountId: "",
        projectId: "",
        applicationMoney: "",
        applicationTime: "",
      },
      selectedStatus: '', // 
      repaymentData: [], // 
      isModalVisible: false, // 
    };
  },
  methods: {
    fetchUserLoans() {
      const userInfo = JSON.parse(localStorage.getItem("userInfo"));
      const userId = userInfo.data.userId;

      axios
          .get(`http://localhost:8080/loanapplication/findbyuserid/${userId}`, {
            headers: {
              role: "USER",
              session: userId,
            },
          })
          .then((response) => {
            this.userLoans = response.data || [];
            this.filterLoans();
          })
          .catch((error) => {
            console.error(":", error);
          });
    },
    fetchProjects() {
      axios
          .get("http://localhost:8080/projects/viewall", { headers: { role: "USER" } })
          .then((response) => {
            this.projects = response.data.data || [];
          })
          .catch((error) => {
            console.error(":", error);
          });
    },
    getStatusText(status) {
      switch (status) {
        case 0:
          return "";
        case 1:
          return "";
        case 2:
          return "";
        default:
          return "";
      }
    },

    //
    filterLoans() {
      if (this.selectedStatus === '') {
        this.filteredLoans = this.userLoans;
      } else {
        //  selectedStatus 
        this.filteredLoans = this.userLoans.filter(loan => loan.status === Number(this.selectedStatus));
      }
    },

    // 
    showRepaymentSchedule(loanId) {
      axios
          .get(`http://localhost:8080/repay/byloanid/${loanId}`)
          .then((response) => {
            this.repaymentData = response.data || [];
            console.log("", this.repaymentData);
            this.isModalVisible = true; // 
          })
          .catch((error) => {
            console.error(":", error);
          });
    },


    // 
    closeModal() {
      this.isModalVisible = false;

    },

    //
    applyLoan() {
      const userInfo = JSON.parse(localStorage.getItem("userInfo"));
      const userId = userInfo.data.userId;

      const applicationData = {
        ...this.loanForm,
        userId: Number(userId),
      };

      axios
          .post("http://localhost:8080/loanapplication/apply", applicationData, {
            headers: {
              role: "USER",
              session: userId,
            },
          })
          .then((response) => {
            console.log(response.data);
            alert(response.data);
            this.fetchUserLoans();
          })
          .catch((error) => {
            console.error(":", error);
            alert("");
          });
    },
  },
  mounted() {
    this.fetchUserLoans();
    this.fetchProjects();
  },
};
</script>

<style scoped>
.loan-projects {
  padding: 20px;
}

.section {
  margin-bottom: 30px;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.section-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.loan-filter {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.select-filter {
  padding: 8px 12px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.loan-table table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.loan-table th,
.loan-table td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

.loan-form .form-group {
  margin-bottom: 15px;
}

.loan-form input {
  padding: 8px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.repayment-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.5); /*  */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.repayment-table {
  width: 80%;
  border-collapse: collapse;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.repayment-table th,
.repayment-table td {
  text-align: center;
  padding: 12px;
  border: 1px solid #ddd;
}

.repayment-table th {
  background-color: #a83434;
  color: white;
  font-weight: bold;
}

.repayment-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.repayment-table tr:hover {
  background-color: #f1f1f1;
}

.no-data {
  text-align: center;
  color: #999;
  font-style: italic;
}

.status-unpaid {
  color: red;
  font-weight: bold;
}

.status-paid {
  color: green;
  font-weight: bold;
}

.modal {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
  width: 600px;
}

/*  */
.close-button {
  position: absolute;
  top: 50px;
  right: 50px;
  background-color:transparent;
  border: none;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  background-color: #a83434;
}

.close-button:hover {
  color: #ff4d4f;
}

/*  */
.repayment-table {
  width: 100%;
  border-collapse: collapse;
}


.submit-btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #0056b3;
}
</style>
