<template>
  <div>
    <div class="top-nav">
      <div class="nav-left">
        <span></span>
      </div>
      <div class="nav-right">
        <span @mouseover="showUserInfo" @mouseleave="handleMouseLeave">{{ adminName || '...' }}</span>
        <!--  -->
        <div  v-if="isUserInfoVisible" class="user-info">
          <p>: {{ adminId }}</p>
          <p>: {{adminName }}</p>
        </div>
        <button @click="logout"></button>
      </div>
    </div>

    <!--  -->
    <div class="admin-content">
      <div class="button-group">
        <button @click="showLoanProjects" :class="{'active': isLoanProjectSelected}">
          
        </button>
        <button @click="showLoanApplications" :class="{'active': isLoanApplicationSelected}">
          
        </button>
      </div>

      <!--  -->
      <div v-if="isLoanProjectSelected">
        <LoanProjectList />
      </div>

      <!--  -->
      <div v-if="isLoanApplicationSelected">
        <LoanApplicationList />
      </div>
    </div>
  </div>
</template>

<script>
import LoanProjectList from './adminLoanProjectList.vue';
import LoanApplicationList from './adminLoanApplicationList.vue';

export default {
  components: {
    LoanProjectList,
    LoanApplicationList,
  },
  data() {
    return {
      isLoanProjectSelected: true,  // 
      isLoanApplicationSelected: false,
      adminInfo: null,
      adminName: "",
      adminId:"",
      isUserInfoVisible: false,
      projects: [],
    };
  },
  mounted() {
    const adminInfoJson = localStorage.getItem("adminInfo");
    if (adminInfoJson) {
      try {

        this.adminInfo = JSON.parse(adminInfoJson);
        console.log(  "",this.adminInfo);
        this.adminName = this.adminInfo.adminName;
        this.adminId=this.adminInfo.adminId;
      } catch (error) {
        alert("");
        this.$router.push({ name: "Login" });
      }
    } else {
      alert("");
      this.$router.push({ name: "Login" });
    }
  },
  methods: {
    showLoanProjects() {
      this.isLoanProjectSelected = true;
      this.isLoanApplicationSelected = false;
    },
    showLoanApplications() {
      this.isLoanProjectSelected = false;
      this.isLoanApplicationSelected = true;
    },
    showUserInfo() {
      console.log("");
      this.isUserInfoVisible = true;
    },
    handleMouseLeave() {
      console.log("");
      this.isUserInfoVisible = false;
    },
    logout() {
      // 
      localStorage.removeItem("adminInfo");
      this.$router.push({ name: "Login" });
    },
  },
};
</script>

<style scoped>
/*  */
.top-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  z-index: 100;
}

.nav-left {
  font-size: 18px;
}
.nav-right {
  display: flex;
  align-items: center;
  position: relative;
}

.nav-right span {
  margin-right: 20px;
  cursor: pointer;
}

.nav-right .user-info {
  position: absolute;
  background-color: #ffffff;
  border: 1px solid #ccc;
  padding: 10px;
  top: 30px; /*  */
  right: 20px;
  width: 200px;
  z-index: 9999;
}
.nav-right .user-info p{
  color:black;
}

.nav-right button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 6px 27px;
  cursor: pointer;
  margin-right: 20px;
}


.admin-content {
  margin-top: 42px;
  padding: 20px;
}

.button-group button {
  padding: 10px 20px;
  margin: 0 10px;
  background-color: #f0f0f0;
  border: none;
  cursor: pointer;
  font-size: 16px;
}
.button-group button.active {
  background-color: #a83434;
  color: white;
}

</style>
