<template>
  <div>
    <div class="top-nav">
      <div class="nav-left">
        <span></span>
      </div>
      <div class="nav-right">
        <span @mouseover="InfoVisible" @mouseleave="handleMouseLeave">{{ clerkName || '...' }}</span>
        <!--  -->
        <div v-if="isUserInfoVisible" class="user-info-popup" @mouseover="InfoVisible" @mouseleave="handleMouseLeave">
          <p>: {{ clerkInfo.clerkId }}</p>
          <p>: {{ clerkInfo.clerkName }}</p>
        </div>
        <button @click="logout"></button>
      </div>
    </div>

    <!--  -->
    <div class="clerk-content">
      <div class="button-group">
        <button @click="showLoanProjects" :class="{'active': isLoanProjectSelected}">
          
        </button>
        <button @click="showUserInfo" :class="{'active': !isLoanProjectSelected}">
          
        </button>
      </div>

      <div class="content-display">
        <!--  -->
        <div v-if="isLoanProjectSelected" class="loan-projects">
          <h2></h2>
          <div v-if="projects.length > 0">
            <table class="loan-table">
              <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="project in projects" :key="project.projectId">
                <td>{{ project.projectId }}</td>
                <td>{{ project.projectName }}</td>
                <td>{{ project.loanmoneyMin }} - {{ project.loanmoneyMax }}</td>
                <td>{{ project.loanmonthMin }} - {{ project.loanmonthMax }}</td>
                <td>{{ project.rateMin }}% - {{ project.rateMax }}%</td>
              </tr>
              </tbody>
            </table>
          </div>
          <div v-else>
            <p></p>
          </div>
        </div>


        <!--  -->
        <div v-else class="user-info">
          <h3></h3>
          <div class="filter-container">
            <!--            <label>-->
            <!--              -->
            <!--              <input type="text" v-model="filterQuery" placeholder="" />-->
            <!--            </label>-->
            <label>
              
              <input type="number" v-model="birthdayFrom" placeholder="" />
              -
              <input type="number" v-model="birthdayTo" placeholder="" />
            </label>
            <label>
              
              <input type="number" v-model="yearIncomeMin" placeholder="" />
              -
              <input type="number" v-model="yearIncomeMax" placeholder="" />
            </label>
            <label>
              
              <input type="number" v-model="workYearsMin" placeholder="" />
              -
              <input type="number" v-model="workYearsMax" placeholder="" />
            </label>

            <label>
              
              <select v-model="sex">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </label>
            <label>
              
              <select v-model="ismarry">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </label>
            <button @click="filterUsers"></button>
            <button @click="resetFilter"></button>
          </div>
          <!--  -->
          <p v-if="noUsersMessage" class="no-users-message">{{ noUsersMessage }}</p>


          <div v-if="!isLoanProjectSelected" class="user-info">

            <div class="user-list">
              <table>
                <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>()</th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
                </thead>
                <tbody>
                <tr
                    v-for="user in allUsers"
                    :key="user.id"
                    :class="{ 'filtered-user': user.isFiltered }"
                >
                  <td>{{ user.userName }}</td>
                  <td>{{ user.cardId }}</td>
                  <td>{{ user.birthday }}</td>
                  <td>{{ user.sex }}</td>
                  <td>{{ user.company }}</td>
                  <td>{{ user.job }}</td>
                  <td>{{ user.yearIncome }}</td>
                  <td>{{ user.email }}</td>
                  <td>{{ user.phone }}</td>
                  <td>{{ user.ismarry }}</td>
                </tr>
                </tbody>

              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      clerkInfo: null,
      clerkName: "",
      isUserInfoVisible: false,
      projects: [],
      filteredUsers: [],//
      filterQuery: "",
      birthdayFrom: "",
      birthdayTo: "",
      yearIncomeMin: "",
      yearIncomeMax: "",
      workYearsMin: "",
      workYearsMax: "",
      sex: "",
      ismarry:"",
      isLoanProjectSelected: true, // 
      allUsers: [], // 
      job:"",
      company:"",
      noUsersMessage: "", // 

    };
  },
  mounted() {
    this.fetchProjects();
    this.fetchAllUsers(); // 
    const clerkInfoJson = localStorage.getItem("clerkInfo");
    if (clerkInfoJson) {
      try {
        this.clerkInfo = JSON.parse(clerkInfoJson);
        this.clerkName = this.clerkInfo.clerkName;
      } catch (error) {
        alert("");
        this.$router.push({name: "Login"});
      }
    } else {
      alert("");
      this.$router.push({name: "Login"});
    }
  },


  methods: {

    handleMouseLeave() {
      this.isUserInfoVisible = false;
    },

    InfoVisible(){
      this.isUserInfoVisible = true;
    },
    //
    fetchProjects() {
      axios
          .get("http://localhost:8080/projects/viewall", {headers: {role: "USER"}})
          .then((response) => {
            this.projects = response.data.data || [];
          })
          .catch((error) => {
            console.error(":", error);
          });
    },
    // 
    fetchAllUsers() {
      axios
          .get("http://localhost:8080/user/findall", { headers: { role: "CLERK" } })
          .then((response) => {
            console.log("",response.data.data)
            this.allUsers = response.data.data || [];
          })
          .catch((error) => {
            console.error(":", error);
          });
    },

    filterUsers() {
      const filters = {
        sex: this.sex || null,
        birthdayFrom: this.birthdayFrom || null,
        birthdayTo: this.birthdayTo || null,
        yearIncomeMin: this.yearIncomeMin || null,
        yearIncomeMax: this.yearIncomeMax || null,
        workYearsMin: this.workYearsMin || null,
        workYearsMax: this.workYearsMax || null,
        ismarry: this.ismarry || null,
        job: this.job || null,
        company: this.company || null,
      };

      axios
          .post("http://localhost:8080/user/filter", filters, {
            headers: { role: "CLERK" },
          })
          .then((response) => {
            const filteredUsers = response.data || [];

            // 
            if (filteredUsers.length === 0) {
              this.noUsersMessage = "";
            } else {
              // 
              this.noUsersMessage = "";
            }

            // 
            filteredUsers.forEach((user) => (user.isFiltered = true));
            // 
            this.allUsers = [
              ...filteredUsers,
              ...this.allUsers.filter((user) => !filteredUsers.some((fUser) => fUser.id === user.id)),
            ];
          })
          .catch((error) => {
            console.error(":", error);
          });
    },

// 
    resetFilter() {
      // 
      this.filterQuery = "";
      this.birthdayFrom = "";
      this.birthdayTo = "";
      this.yearIncomeMin = "";
      this.yearIncomeMax = "";
      this.workYearsMin = "";
      this.workYearsMax = "";
      this.sex = "";
      this.ismarry = "";
      this.job = "";
      this.company = "";
      this.noUsersMessage="";
      // 
      this.fetchAllUsers();
    },
    logout() {
      localStorage.clear();
      this.$router.push({name: "Login"});
    },
    showLoanProjects() {
      this.isLoanProjectSelected = true;
    },
    showUserInfo() {
      this.isLoanProjectSelected = false;
    },
  },
};
</script>

<style scoped>
.top-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  z-index: 100;
}

.nav-left {
  font-size: 18px;
}

.nav-right {
  display: flex;
  align-items: center;
}
.user-info-popup {
  position: absolute;
  top: 40px;
  right: 20px;
  background-color: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  width: 250px;
  z-index: 200;
  animation: fadeIn 0.3s ease;
}





.user-info-popup p {
  margin: 5px 0;
  color: #000000;
}

.nav-right span {
  margin-right: 20px;
  cursor: pointer;
}
.filtered-user {
  background-color: #e6f7ff; /*  */
  font-weight: bold;
}




.nav-right button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 6px 27px;
  cursor: pointer;
  margin-right: 20px;
}

.clerk-content {
  margin-top: 64px;
}

.button-group {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.button-group button {
  margin: 0 10px;
  padding: 10px 20px;
  border: none;
  background-color: #ddd;
  cursor: pointer;
}

.button-group button.active {
  background-color: #007bff;
  color: white;
}

.content-display {
  padding: 20px;
}

.loan-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.loan-table th, .loan-table td {
  padding: 10px;
  border: 1px solid #ddd;
}

.loan-table th {
  background-color: #f4f4f4;
}

.user-list table {
  width: 100%;
  border-collapse: collapse;
}

.user-list th, .user-list td {
  padding: 10px;
  border: 1px solid #ddd;
}
</style>
