<template>
  <div class="loan-applications">
    <h2></h2>

    <!--  -->
    <div class="filter-container">
      <select v-model="statusFilter" @change="fetchLoanApplications">
        <option value="all"></option>
        <option value="havenotassessed"></option>
        <option value="notapproved"></option>
        <option value="approved"></option>
      </select>
    </div>

    <!--  -->
    <table v-if="loanApplications.length > 0">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="application in loanApplications" :key="application.id">
        <td>{{ application.applicantName }}</td>
        <td>{{ application.applicationmoney}}</td>
        <td>{{ application.applicationtime }}</td>
        <td>{{ application.loanRate }}</td>
        <td>{{ getStatusText(application.status) }}</td>
      </tr>
      </tbody>
    </table>
    <p v-else></p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loanApplications: [],
      statusFilter: "all",  // 
    };
  },
  methods: {
    // 
    fetchLoanApplications() {
      let url = `http://localhost:8080/loanapplication/find/all`;  // 
      if (this.statusFilter === "havenotassessed") {
        url = "http://localhost:8080/loanapplication/find/havenotassessed";
      } else if (this.statusFilter === "notapproved") {
        url = "http://localhost:8080/loanapplication/find/notapproved";
      } else if (this.statusFilter === "approved") {
        url = "http://localhost:8080/loanapplication/find/approved";
      }

      // API
      fetch(url, {
        method: "GET",
        headers: {
          role: "ADMINISTRATOR",
        },
      })
          .then((response) => response.json())
          .then((data) => {
            this.loanApplications = data || [];
            console.log("", this.loanApplications );
            // userId
            this.fetchUserNames();
          })
          .catch((error) => {
            console.error(":", error);
          });
    },
// 
    fetchUserNames() {
      this.loanApplications.forEach((application) => {
        const userId = application.userId;
        console.log( userId);

        // userId
        fetch(`http://localhost:8080/user/findbyuserid/${userId}`, {
          method: "GET",
        })
            .then((response) => response.json())
            .then((userData) => {
              // 
              console.log("!",userData)
              application.applicantName = userData.data.userName;
            })
            .catch((error) => {
              console.error(` (UserId: ${userId}):`, error);
            });
      });
    },
    // 
    getStatusText(status) {
      switch (status) {
        case 0:
          return "";
        case 1:
          return "";
        case 2:
          return "";
        default:
          return "";
      }
    },
  },
  // 
  mounted() {
    this.fetchLoanApplications();
  },
};
</script>

<style scoped>
.loan-applications {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

h2 {
  text-align: center;
  color: #a83434;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  padding: 12px 15px;
  text-align: left;
  border: 1px solid #ddd;
}

th {
  background-color: #a83434;
  color: white;
  font-weight: bold;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #f1f1f1;
}

.filter-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

select {
  padding: 4px;
  font-size: 16px;
  margin-right: 20px;
}
</style>
